"use client";
import { useState } from "react";

interface Tournament {
  name: string;
  system: string;
  time: number;
  creator: string;
}

export default function TournamentsPage() {
  const [tournaments, setTournaments] = useState<Tournament[]>([
    { name: "Torneo Inicial", system: "Round-robin", time: 10, creator: "Admin" },
  ]);

  return (
    <div style={{ maxWidth: "700px", margin: "3rem auto", display: "flex", flexDirection: "column", gap: "1rem" }}>
      <h2 style={{ color: "#4f46e5" }}>Torneos</h2>
      {tournaments.map((t, i) => (
        <div key={i} style={{ backgroundColor: "#fff", padding: "1rem", borderRadius: "8px", boxShadow: "0 0 5px rgba(0,0,0,0.1)" }}>
          <h3>{t.name}</h3>
          <p>Sistema: {t.system}</p>
          <p>Tiempo: {t.time} min</p>
          <p>Creador: {t.creator}</p>
          <button>Emparejamientos</button>
        </div>
      ))}
    </div>
  );
}
